import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			tabindex:0,
			tablist:['我的收藏',"发布历史"],
			voteList:[],
			votetop:false,
			voteallowtop:true,
			votebottom:false,
			voteallowbottom:false,
			voteId:'',
			historyList:[],
			historyId:'',
			historytop:false,
			historyallowtop:true,
			historybottom:false,
			historyallowbottom:false,
		}
	},
	onShow() {
		
		uni.showLoading({
		    title: '加载中'
		});
		this.voteId = ''
		this.historyId = ''
		this.getVote()
		this.getHistory()
		
	},
	onLoad(e) {
		
	},
	methods: {
		edit(id){
			uni.navigateTo({
				url:'../../pagesMy/EditMyProject/EditMyProject?id='+id
			})
		},
		// 发布
		Issue(){
			uni.navigateTo({
				url:'../../pagesMy/MyProjectChoose/MyProjectChoose'
			})
		},
		bottomhis(){
			if(this.historyallowbottom){
				this.historybottom = true
				this.historyallowbottom = false
				this.voteId = this.voteList[this.historyList.length-1].id
				setTimeout(()=> {
					this.getHistory()
				}, 1000);
				
			}
		},	
		tophis(){
			if(this.historyallowtop){
				this.historytop = true
				this.historyallowtop =false
				this.historyId=''
				this.getHistory()
			}
		},
		bottomvote(){
			if(this.voteallowbottom){
				this.votebottom = true
				this.voteallowbottom = false
				this.voteId = this.voteList[this.voteList.length-1].vote_id
				setTimeout(()=> {
					this.getVote()
				}, 1000);
				
			}
		},
		topvote(){
			if(this.voteallowtop){
				this.votetop = true
				this.voteallowtop =false
				this.voteId=''
				this.getVote()
			}
		},
		del(id){
			Dialog.confirm({
			  title: '提示',
			  message: '你确定要取消收藏吗？'
			  // confirmButtonText:'我要拨打'
			}).then(() => {
				this.voteList = this.voteList.filter((item,index)=>item.id != id)
				this.$api.Opportunity_vote({
				  user_id: uni.getStorageSync("userId"),
				  token: uni.getStorageSync("token"),
				  id:id
				}).then(res => {
					
				  if (res.data.code === '1039') {
						uni.showToast({
							title: '取消收藏成功',
							duration: 2000,
							icon:'none'
						});
					  }
				  })
				
			}).catch(() => {
			  // on cancel
			});
			
		},
		del2(id){
			Dialog.confirm({
			  title: '提示',
			  message: '你确定要删除吗？'
			  // confirmButtonText:'我要拨打'
			}).then(() => {
				
				this.$api.Opportunity_del_op({
				  user_id: uni.getStorageSync("userId"),
				  token: uni.getStorageSync("token"),
				  id:id
				}).then(res => {
					uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon:'none'
					});
				  if (res.data.code === '200') {
						this.historyList = this.historyList.filter((item,index)=>item.id != id)
					  }
				  })
				
			}).catch(() => {
			  // on cancel
			});
			
		},
		// 详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 关注的
		getVote(){
			// console.log(this.voteId)
			this.$api.myself_vote({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				page_id:this.voteId
			}).then(res=>{
				uni.hideLoading();
				if(!this.voteallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.votetop = false
						this.voteallowtop = true
					}, 2000);
				}
				if(!this.voteId){
					if(res.data.data.length>=20){
						this.voteList = res.data.data
						this.voteallowbottom = true
						
					}else{
						this.voteList = res.data.data
						this.voteallowbottom = false
					}														
				}else{
					
					if(res.data.data.length>=20){
						this.voteList = this.voteList.concat(res.data.data)
						// console.log(this.voteList)
						this.voteallowbottom = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
						 
					}else{
						this.voteList = this.voteList.concat(res.data.data)
						this.voteallowbottom = false
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.votebottom = false
						}, 1000);
						
					}
					
					
				}
				
				
			})
		},
		// 历史
		getHistory(){
			this.$api.myself_Oppor({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				page_id:this.historyId
			}).then(res=>{

				uni.hideLoading();
				if(!this.historyallowtop){
					setTimeout(()=>{
						uni.showToast({
						    title: '刷新成功',
						    duration: 1000,
							icon:'none'
						});
						this.historytop = false
						this.historyallowtop = true
					}, 2000);
				}
				if(!this.historyId){
					if(res.data.data.length>=20){
						this.historyList = res.data.data
						this.historyallowbottom = true
						
					}else{
						this.historyList = res.data.data
						this.historyallowbottom = false
					}														
				}else{
					
					if(res.data.data.length>=20){
						this.historyList = this.historyList.concat(res.data.data)
						
						this.historyallowbottom = true
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						 
					}else{
						this.historyList = this.historyList.concat(res.data.data)
						this.historyallowbottom = false
						setTimeout(()=>{
							uni.showToast({
							    title: '加载成功',
							    duration: 1000,
								icon:'none'
							});
							this.historybottom = false
						}, 1000);
						
					}
					
					
				}
			})
		},
		changeSwiper(e){
			this.tabindex = e.detail.current
		},
		tab(ind){
			this.tabindex = ind
		}
	}
}
